      type: Number,
      default: 0
    },
    isUpdate: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      touchParams: {
        startY: 0,
        endY: 0,
        startTime: 0,
        endTime: 0
      },
      currIndex: 1,
      transformY: 0,
      scrollDistance: 0,
      lineSpacing: 36,
      rotation: 20,
      timer: null
    };
  },
  watch: {
    isUpdate: function isUpdate() {
      this.transformY = 0;
      this.modifyStatus();
    },
    defaultValue: function defaultValue() {
      this.transformY = 0;
      this.modifyStatus();
    }
  },
  methods: {
    updateTransform: function updateTransform(value) {
      var _this = this;

      if (value) {
        this.transformY = 0;
        this.timer = setTimeout(function () {
          _this.modifyStatus(null, value);
        }, 10);
