import locale from '../../mixins/locale';

var lockMaskScroll = function (bodyCls) {
  var scrollTop;
  return {
    afterOpen: function afterOpen() {
      scrollTop = document.scrollingElement.scrollTop || document.body.scrollTop;
      document.body.classList.add(bodyCls);
      document.body.style.top = -scrollTop + 'px';
    },
    beforeClose: function beforeClose() {
      if (document.body.classList.contains(bodyCls)) {
        document.body.classList.remove(bodyCls);
        document.scrollingElement.scrollTop = scrollTop;
      }
    }
  };
}('dialog-open');

export default {
  name: 'nut-dialog',
  mixins: [locale],
  props: {
    id: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    link: {
      type: String,
      default: ''
    },
    imgSrc: {
      type: String,
      default: ''
    },
    animation: {
      type: Boolean,
      default: true
    },
    lockBgScroll: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean,
      default: false
    },
    closeBtn: {
      type: Boolean,
      default: false
    },
    closeOnClickModal: {
      type: Boolean,
      default: true
    },
    noFooter: {
      type: Boolean,
      default: false
    },
    noOkBtn: {
      type: Boolean,
      default: false
    },
    noCancelBtn: {
      type: Boolean,
      default: false
    },
    cancelBtnTxt: {
      type: String,
      default: ''
    },
    okBtnTxt: {
      type: String,
      default: ''
    },
    okBtnDisabled: {
      type: Boolean,
      default: false
    },
    cancelAutoClose: {
      type: Boolean,
      default: true
    },
    textAlign: {
      type: String,
      default: 'center'
    },
    onOkBtn: {
      type: Function,
      default: null
    },
    onCloseBtn: {
      type: Function,
      default: null
    },
    onCancelBtn: {
      type: Function,
      default: null
    },
    closeCallback: {
      type: Function,
      default: null
    },
    onClickImageLink: {
      type: Function,
      default: null
    },
    maskBgStyle: {
      type: String,
      default: ''
    },
    canDestroy: {
      type: Boolean,
      default: true
    },
    customClass: {
      type: String,
      default: ''
    }
  },
  data: function data() {
    return {
      curVisible: false,
      destroy: false
    };
  },
  created: function created() {
    this.destroy = true;
  },
  methods: {
    modalClick: function modalClick() {
      if (!this.closeOnClickModal) {
        return;
      }

      this.close('modal');
    },
    todestroy: function todestroy() {
      this.canDestroy ? '' : this.destroy = false;
    },
    close: function close(target) {
      this.$emit('close', target);
      this.$emit('close-callback', target);
      this.todestroy();

      if (typeof this.closeCallback === 'function' && this.closeCallback(target) === false) {
        return;
      }

      this.curVisible = false;
    },
    okBtnClick: function okBtnClick() {
      this.$emit('ok-btn-click');

      if (typeof this.onOkBtn === 'function') {
        this.onOkBtn.call(this);
      }
    },
    cancelBtnClick: function cancelBtnClick(autoClose) {
      this.$emit('cancel-btn-click');

      if (!autoClose) {
        return;
      }

      if (typeof this.onCancelBtn === 'function') {
        if (this.onCancelBtn.call(this) === false) {
          return;
        }
      }

      this.close('cancelBtn');
    },
    closeBtnClick: function closeBtnClick() {
      if (typeof this.onCloseBtn === 'function') {
        if (this.onCloseBtn.call(this) === false) {
          return;
        }
      }

      this.close('closeBtn');
    },
    //图片类型弹窗中的链接点击事件，默认跳转
    imageLinkClick: function imageLinkClick() {
      if (this.onClickImageLink && this.onClickImageLink.call(this) === false) {
        return;
      }

      if (this.link) {
        location.href = this.link;
      }
    }
  },
  watch: {
    visible: {
      handler: function handler(val) {
        this.curVisible = val;
      },
      immediate: true
    },
    curVisible: function curVisible(val) {
      if (this.lockBgScroll) {
        //锁定or解锁页面滚动
        lockMaskScroll[val ? 'afterOpen' : 'beforeClose']();
      }
    }
  }
};